.search-option {
	position: absolute;
	background-color: #000;
	overflow: hidden;
	align-items: center;
	color: #fff;
	width: 200px;
	height: 200px;
	border-radius: 49% 51% 50% 50% / 68% 69% 31% 32%;
	left: 30%;
	bottom: -250px;
	transition: all 0.5s;
	cursor: pointer
}

.search-option .first-search {
	position: absolute;
	top: 20px;
	left: 90px;
	font-size: 20px;
	opacity: 1000
}

.search-option .inputs {
	opacity: 0;
	transition: all 0.5s ease;
	transition-delay: 0.5s;
	position: relative
}

.search-option .inputs input {
	position: absolute;
	top: 200px;
	left: 30px;
	padding-left: 20px;
	background-color: transparent;
	width: 300px;
	border: none;
	color: #fff;
	border-bottom: 1px solid #eee;
	transition: all 0.5s;
	z-index: 10
}

.search-option .inputs input:focus {
	box-shadow: none;
	outline: none;
	z-index: 10
}

.search-option:hover {
	border-radius: 0px;
	width: 100%;
	left: 0px
}

.search-option:hover .inputs {
	opacity: 1
}

.search-option:hover .first-search {
	left: 27px;
	top: 25px;
	font-size: 15px
}

.search-option:hover .inputs input {
	top: 20px
}

.search-option .share {
	position: absolute;
	right: 20px;
	top: 22px
}

